@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
body {
  margin: 0;
  background-color: #0f0e13;
  background-color: var(--backgroundColor);
}

/*--accent: #4ad1f3;
  --secondaccent: #121ca8;*/

:root {
  --accent: #f34a4a;
  --secondaccent: #a81212;
  --darkAccent: #6c0b0b;
  --glassContainerColor: #241512;
  --font: "Montserrat";
  --secondaryText: rgb(103, 103, 103);
  --text: white;
  --containerColor: rgb(13, 21, 27);
  --backgroundColor: #0f0e13;
  --headerHeight: 50px;
}

.container {
  background: rgb(13, 21, 27);
  background: var(--containerColor);
  width: 100%;
  height: 100%;
  border-radius: 1.5rem;
  display: flex;
  align-items: space-between;
  justify-content: space-between;
  flex-direction: column;

  padding: 20px;

  color: #fff;

  box-sizing: border-box;
  position: relative;

  grid-gap: 5px;

  gap: 5px;
}
.containerHighlight {
  background: linear-gradient(
    146.1deg,
    #404345 2.22%,
    rgba(52, 52, 52, 0) 89.02%
  );
  padding: 30px;
  justify-content: flex-start;
}
html,
body {
  max-width: 100%;
  overflow-x: hidden;
}
.maxWidthContainer {
  max-width: 1300px;
  width: 100%;
  margin: auto;
  padding: 10px 30px;
  box-sizing: border-box;
}
.imgContainer {
  background-color: rgb(13, 21, 27);
  background-color: var(--containerColor);
  width: 100%;
  height: 100%;
  border-radius: 1rem;
  display: flex;
  align-items: space-between;
  justify-content: space-between;
  flex-direction: column;

  padding: 15px;

  color: #fff;

  box-sizing: border-box;
  position: relative;

  grid-gap: 5px;

  gap: 5px;
}
.container.highlightContainer {
  border: 1px solid #b6b6b6;
  align-items: center;
  justify-content: center;
}
.containerGraph {
  min-width: 100px;
  height: 200px;
  display: flex;
  flex-direction: column;
  grid-gap: 10px;
  gap: 10px;
}
.containerRow {
  display: flex;
  flex-direction: row;
  grid-gap: 10px;
  gap: 10px;
  justify-content: center;
  align-items: center;
}
.colorContainer {
  background: #f34a4a;
  background: var(--accent);
  background: linear-gradient(
    135deg,
    #f34a4a 0%,
    #a81212 100%
  );
  background: linear-gradient(
    135deg,
    var(--accent) 0%,
    var(--secondaccent) 100%
  );

  width: 100%;
  height: 100%;
  border-radius: 1.5rem;
  display: flex;
  align-items: space-between;
  justify-content: space-between;
  flex-direction: column;

  padding: 25px;

  color: #fff;

  box-sizing: border-box;
  position: relative;

  grid-gap: 5px;

  gap: 5px;
}
.cautionContainer {
  width: 100%;
  background-color: brown;
  border-radius: 1.5rem;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: row;
  padding: 3px 7px;
  color: #fff;
  box-sizing: border-box;
  position: relative;
  font-family: "Montserrat";
  font-size: 12px;
  grid-gap: 5px;
  gap: 5px;
  margin-bottom: 5px;
}
.secondColorContainer {
  background-color: #0f0e13;
  background-color: var(--backgroundColor);
  background-image: linear-gradient(
    89.7deg,
    rgba(0, 0, 0, 0) 10%,
    rgba(53, 92, 125, 1) 88.8%
  );
  overflow: hidden;
}
.cardContainer {
  width: 200px;
  min-width: 200px;
  height: 250px;
  background-color: rgb(13, 21, 27);
  background-color: var(--containerColor);
  border-radius: 1rem;
}
.fadeBackgroundSection {
  width: 100vw;
  box-sizing: border-box;

  background: linear-gradient(
    180deg,
    rgba(255, 255, 255, 0) 0%,
    rgba(255, 255, 255, 0.06) 12.48%,
    rgba(255, 255, 255, 0.06) 86.49%,
    rgba(255, 255, 255, 0) 100%
  );
}

/*------------------------------Containers------------------------------*/

.containerHead {
  display: flex;
  justify-content: space-between;
}
.containerHead .btn-underline {
