  grid-template-columns: 1fr 1fr;
  grid-gap: 10px;
  gap: 10px;
}
.column21 {
  display: grid;
  grid-template-columns: 2fr 1fr;
  grid-gap: 10px;
  gap: 10px;
}
.column12 {
  display: grid;
  grid-template-columns: 1fr 2fr;
  grid-gap: 10px;
  gap: 10px;
}
.column23 {
  display: grid;
  grid-template-columns: 2fr 3fr;
  grid-gap: 10px;
  gap: 10px;
}
.column111 {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 10px;
  gap: 10px;
  transition: 0.3;
}
.column1111 {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 10px;
  gap: 10px;
}
.column121 {
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  grid-gap: 10px;
  gap: 10px;
}
.column211 {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr;
  grid-gap: 10px;
  gap: 10px;
}
.flexContainer {
  display: flex;
  flex-direction: column;
  text-align: left;
}
.flex {
  display: flex;
  flex-direction: row;
  align-items: center;
  grid-gap: 7px;
  gap: 7px;
}
.center {
  display: flex;
  align-items: center;
  justify-content: center;
}
.Vcenter {
  display: flex;
  justify-content: center;
  height: inherit;
  box-sizing: border-box;
}
.VcenterFull {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
  box-sizing: border-box;
}

.containerOverlay {
  display: none;
  position: absolute;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  top: 0;
  left: 0;
  border-radius: 1.5rem;
  z-index: 105;
  justify-content: center;
  align-items: center;
}

#hT1:hover .containerOverlay {
  display: flex;
}
.iconContainer {
  height: 30px;
  width: 30px;
  border-radius: 500px;
  background-color: #cdd8f5;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}
.colorHover:hover {
  cursor: pointer;
  background-color: #f1f4fa;
}

@media only screen and (max-width: 800px) {
  .flexContainer {
    align-items: center;
    text-align: center;
  }
  .flexContainer img {
    padding: 20px;
  }

  .containerHead h2 {
    font-size: 14px;
    width: 80%;
  }
  .containerHead .btn-underline {
    margin-top: 2px;
    font-size: 10px;
    width: 60%;
  }
  .containerHead .btn-underline::after {
    padding-right: 5px;
    padding-left: 5px;
  }
}

/*------------------------------Buttons------------------------------*/
.btn {
  background: #f34a4a;
  background: var(--accent);
  background: linear-gradient(
    135deg,
    #a81212 0%,
    #f34a4a 100%
  );
  background: linear-gradient(
    135deg,
    var(--secondaccent) 0%,
    var(--accent) 100%
  );
  padding: 10px 15px;
  border-radius: 7px;

  display: flex;
  justify-content: center;
  align-items: center;

  font-family: "Montserrat";

  font-family: var(--font);
  color: #fff;
  font-size: 18px;
  font-weight: 500;
  letter-spacing: 0.5px;

  cursor: pointer;
  transition: 0.3s;
  border: none;
}
.btn:hover {
  opacity: 0.7;
}
.roundBtn {
  border-radius: 50px;
  padding: 15px 20px;
}
.bBtn {
  border: 1px solid #f34a4a;
  border: 1px solid var(--accent);
  padding: 8px 15px;
  border-radius: 5px;

  font-family: "Montserrat";

  font-family: var(--font);
  color: #76a3d6;
  font-size: 14px;
  font-weight: 400;
  letter-spacing: 1px;

  display: flex;
  justify-self: center;
  align-items: center;

  cursor: pointer;
  transition: 0.3s;
}
.bBtn:hover,
.bBtn.selected {
  color: #fff;
  background: #f34a4a;
  background: var(--accent);
  opacity: 1;
}
.btn-underline {
  text-decoration: none;
  padding: 7px 15px;
  padding-top: 0;
  color: rgb(103, 103, 103);
  color: var(--secondaryText);
  position: relative;

  font-size: 14px;

  cursor: pointer;
  font-family: "Montserrat";
  font-weight: 600;

  text-align: center;

  transition: 0.3s;
}
.btn-underline:hover {
  color: #fff;
}
.btn-underline::after {
  bottom: 3px;
  content: "";
  display: block;
  height: 2px;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  position: absolute;

  transition: width 0.3s ease 0s, left 0.3s ease 0s;
  transition: 0.3s;
  width: 0px;
}
.btn-underline:hover::after {
  width: calc(90% - 10px);
  background: rgb(255, 255, 255);
}
.btn-underline-color {
  color: #6c0b0b;
  color: var(--darkAccent);
}
.btn-underline-color:hover {
  color: #6c0b0b;
  color: var(--darkAccent);
}
.btn-underline-color:hover::after {
  background: #6c0b0b;
  background: var(--darkAccent);
}
.noBtn {
  color: rgb(103, 103, 103);
  color: var(--secondaryText);
  border: 1px solid rgb(103, 103, 103);
  border: 1px solid var(--secondaryText);
  background: #17171700 !important;
}
.noBtn:hover {
  cursor: default;
  background-color: inherit;
  opacity: 0.5;
  color: rgb(103, 103, 103);
  color: var(--secondaryText);
}
.btnGreen {
  background: linear-gradient(135deg, #15d115 0%, #1a7d06 100%);
}
.btnRed {
  background: linear-gradient(135deg, #ff0505 0%, #a32414 100%);
}
.btnBlack {
  background: linear-gradient(135deg, #202020c0 0%, #000000c0 100%);
}
.btnWhite {
  background: linear-gradient(135deg, #ffffffc0 0%, #d8d8d8c0 100%);
  color: black;
}
.iconBtn {
  width: 35px;
  height: 35px;
  color: rgb(255, 255, 255);
  background-color: rgba(255, 255, 255, 0);
  padding: 10px;
  border-radius: 50px;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: 0.3s;
}
.iconBtn:hover {
  color: rgb(0, 0, 0);
  background-color: rgba(255, 255, 255, 0.685);
}
.btn-fade {
  text-decoration: none;
  padding: 7px 15px;
  padding-top: 0;
  color: rgb(103, 103, 103);
  color: var(--secondaryText);
  position: relative;

  font-size: 14px;

  cursor: pointer;
  font-family: "montserrat";
  font-weight: 600;

  text-align: center;

  transition: 0.3s;
}
.btn-fade:hover {
  color: #fff;
}
.uploadBtn {
  padding: 10px 15px;
}

