/*------------------------------ScrollContainer------------------------------*/
.HScrollContainer {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  overflow: auto;
  grid-gap: 5px;
  gap: 5px;
}
.HScrollContainer::-webkit-scrollbar-track {
  border-radius: 10px;
  border: rgb(77, 77, 77) 1px;
}
.HScrollContainer::-webkit-scrollbar {
  width: 5px;
  height: 7px;
  position: absolute;
}
.HScrollContainer::-webkit-scrollbar-thumb {
  border-radius: 5px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #f34a4a;
  background-color: var(--accent);
  height: 5px;
}
.HScrollContainer::-webkit-scrollbar-thumb:hover {
  background-color: #a81212;
  background-color: var(--secondaccent);
}

.scrollbar::-webkit-scrollbar-track {
  border-radius: 10px;
  border: rgb(77, 77, 77) 1px;
}
.scrollbar::-webkit-scrollbar {
  width: 10px;
  height: 7px;
  position: absolute;
}
.scrollbar::-webkit-scrollbar-thumb {
  border-radius: 5px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #f34a4a;
  background-color: var(--accent);
  height: 5px;
}
.scrollbar::-webkit-scrollbar-thumb:hover {
  background-color: rgb(12, 103, 131);
}

.VScrollContainer {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  overflow: auto;
  grid-gap: 5px;
  gap: 5px;
  padding: 5px;
}
.VScrollContainer::-webkit-scrollbar-track {
  border-radius: 10px;
  border: rgb(77, 77, 77) 1px;
}
.VScrollContainer::-webkit-scrollbar {
  width: 7px;
  height: 7px;
  position: absolute;
}
.VScrollContainer::-webkit-scrollbar-thumb {
  border-radius: 5px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #f34a4a;
  background-color: var(--accent);
  height: 5px;
}
.VScrollContainer::-webkit-scrollbar-thumb:hover {
  background-color: #a81212;
  background-color: var(--secondaccent);
}
/*------------------------------content------------------------------*/
.content {
  box-sizing: border-box;
  position: relative;
  width: 100%;
  bottom: 0;
  padding: 20px;
  display: flex;
  flex-direction: column;
  grid-gap: 10px;
  gap: 10px;
}
.fullContent {
  position: absolute;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: 170px 1fr;
}

/*------------------------------Ripple------------------------------*/
#rippleEffectBubble {
  position: absolute;
  background-color: rgb(255, 255, 255);
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  pointer-events: none;
  border-radius: 50%;
  -webkit-animation: animate 1s linear infinite;
          animation: animate 1s linear infinite;
}
@-webkit-keyframes animate {
  0% {
    width: 0px;
    height: 0px;
    opacity: 0.5;
  }
  100% {
    width: 500px;
    height: 500px;
    opacity: 0;
  }
}
@keyframes animate {
  0% {
    width: 0px;
    height: 0px;
    opacity: 0.5;
  }
  100% {
    width: 500px;
    height: 500px;
    opacity: 0;
  }
}
/*------------------------------ LOADING / LIVE ------------------------------*/
.loading {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
  left: 50%;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
  -webkit-transform: scale(0.5, 0.5);
          transform: scale(0.5, 0.5);
}
.loading div {
  position: absolute;
  border: 4px solid rgb(255, 255, 255);
  opacity: 1;
  border-radius: 50%;
  -webkit-animation: loading 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
          animation: loading 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}
.loading div:nth-child(2) {
  -webkit-animation-delay: -0.5s;
          animation-delay: -0.5s;
}
@-webkit-keyframes loading {
  0% {
    top: 36px;
    left: 36px;
    width: 0;
    height: 0;
    opacity: 1;
  }
  100% {
    top: 0px;
    left: 0px;
    width: 72px;
    height: 72px;
    opacity: 0;
  }
}
@keyframes loading {
  0% {
    top: 36px;
    left: 36px;
    width: 0;
    height: 0;
    opacity: 1;
  }
  100% {
    top: 0px;
    left: 0px;
    width: 72px;
    height: 72px;
    opacity: 0;
  }
}

.livenow {
  width: 20px;
  padding: 5px;
  margin: 0 auto;
  display: inline-block;
  -webkit-transform: scale(0.5);
          transform: scale(0.5);
}
.livenow > div {
  vertical-align: middle;
  width: 22px;
  height: 22px;
  border-radius: 100%;
  position: absolute;
  margin: 0 auto;
  border: 3px solid rgba(255, 255, 255, 1);
  -webkit-animation: live 1.4s infinite ease-in-out;
  animation: live 1.4s infinite ease-in-out;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}
.livenow > div:nth-child(1) {
  background-color: rgba(255, 255, 255, 0.3);
  background-color: rgba(255, 255, 255, 1);
  -webkit-animation-delay: -0.1s;
  animation-delay: -0.1s;
}
.livenow > div:nth-child(2) {
  -webkit-animation-delay: 0.16s;
  animation-delay: 0.16s;
}
.livenow > div:nth-child(3) {
  -webkit-animation-delay: 0.42s;
  animation-delay: 0.42s;
  border: 3px solid rgba(255, 255, 255, 0.5);
}
.livenow > div:nth-child(4) {
  border: 3px solid rgba(255, 255, 255, 1);
  -webkit-animation-delay: -0.42s;
  animation-delay: -0.42s;
}

@-webkit-keyframes live {
  0%,
  80%,
  100% {
    -webkit-transform: scale(0.6);
  }
  40% {
    -webkit-transform: scale(1);
  }
}
@keyframes live {
  0%,
  80%,
  100% {
    transform: scale(0.6);
    -webkit-transform: scale(0.6);
  }
  40% {
    transform: scale(1);
    -webkit-transform: scale(1);
  }
}

